body{
  background-color: rgb(13, 13, 75);
}
.main{
  margin: 0 auto;
  text-align: center;
}
.area{
  position: absolute;
  margin-right: 15%;
  margin-left: 15%;
  margin-top: 21%;
  width: 70%;
  height: 45%;
  background-color: #000;
  border: 2px solid #000;
}
.main-dot{
  position: absolute;
  border-radius: 55%;
  width:3%;
  height: 3%;
  background-color: rgb(196, 29, 29);
  border: 1px solid #fff;
  z-index: 2;
}

.main-food{
  position: absolute;
  width: 3%;
  height: 3%;
  border-radius: 55%;
  background-color: darkgreen;
  border: 1px solid #fff;
  z-index: 1;
}
.footer{
  position: absolute;
  margin-top: 20%;
  color: red;
  font-size: xx-large;
}
.head{
  font-size: 300%;
  color: red;
  text-align: center;
}
.bt{
  padding: 2%;
}
.controller{
  margin-top: 90%;
  text-align: center;

}
.button{
    padding:6%;
